@value font-title, font-mMedium, font-mSmall, font-btn from '../../common/size.css';
@value color-darkBlack, color-grey from '../../common/colors.css';

.titleContianer {
  margin-top: 35px;
  padding: 20px;
  position: relative;
}
.titleContianer .title {
  margin: 0;
  font-size: font-title;
  font-weight: 700;
}

.titleContianer p {
  margin-top: 5px;
  color: color-grey;
  font-size: font-mMedium;
}

.titleContianer button {
  border: none;
  color: color-grey;
  font-size: font-mMedium;
  background-color: transparent;
  position: absolute;
  right: 20px;
  top: 25px;
}

.itemsContainer {
  width: 100%;
  overflow: scroll;
}
.itemsContainer::-webkit-scrollbar {
  display: none;
  width: 0 !important;
}
.items {
  display: flex;
  width: fit-content;
  margin: 0 10px;
}

.items .item {
  display: flex;
  flex-direction: column;
  width: 115px;
  height: 210px;
  margin: 0 10px;
}

.item .imgbox {
  height: 115px;
}

.item .img {
  width: 100%;
}

.item h3,
p {
  margin: 0;
}

.item .name {
  font-size: font-mMedium;
  font-weight: 400;
  margin-top: 5px;
}

.itemInfo {
  margin-top: auto;
}

.item p {
  font-size: font-mLarge;
  font-weight: 600;
}

.item p span:nth-child(1) {
  font-size: font-mSmall;
  margin-bottom: 5px;
}

.item p span:nth-child(2) {
  color: color-grey;
  font-size: font-mSmall;
}
